@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@700&family=Poppins:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@300&display=swap');



h2 {
  color: rgb(255, 234, 0);
  text-shadow: 0 0 5px  rgb(255, 208, 0),
  0 0 60px #ffffff79;
  font-family: 'orbitron' !important;
  font-size: 25px;
  text-transform: uppercase;
  white-space: nowrap;
  padding: 0 6rem;
  letter-spacing: 1px;
  animation: move-rtl 20000ms linear infinite;
}

@keyframes move-rtl {
0%{
  transform: translateX(0);
}
100%{
  transform: translateX(-100%);
}

}




.bg_container {
  background-color: black;
  height: 100vh;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

section {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  transform-style: preserve-3d;

}

.green {
  background: none;
  color: rgb(0, 255, 123);
  text-shadow: 0 0 10px rgb(0, 255, 123),
    0 0 50px rgb(0, 255, 123),
    0 0 60px #ffffff79,
    0 0 90px #3d3d3d5a;
}

.red {
  background: none;
  color: rgb(255, 0, 0);
  text-shadow: 0 0 10px rgb(255, 0, 0),
    0 0 60px #dc0000c6,
    0 0 90px #6500005a;

}


h1 {
  background: none;
  font-family: orbitron;
  font-weight: 900;
  font-size: 40px;
  margin-top: 2rem;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  position: relative;
}

h1 span:first-child {
  background: none;
  font-size: 35px;
}

h1 span:last-child {
  color: white;
  background: none;
  font-size: 60px;
  margin-left: none;
  position: relative;
  top: -50px;
  left: 10px;
  text-shadow: 0 0 0px #ffffff,
    0 0 30px #ffffffb8,
    0 0 60px #78787879,
    0 0 90px #3d3d3d5a;
}

.candlesticks {
  height: 80px;
  margin-left: 20px;
  background: none;
}



.moon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 300px;
  height: 300px;
  background: url(/images/moon.jpg);
  background-size: cover;
  background-repeat: repeat-x;
  border-radius: 50%;
  box-shadow: inset 0 0 20px rgba(0, 0, 0, 1), 0 0 110px #ffffffa0;
  animation: animateMoon 200s linear infinite;
}

@keyframes animateMoon {
  0% {
    background-position: 0 0;
  }

  100% {
    background-position: -4096px 0;
  }

}


.content-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 65%;
}

img {
  max-height: 45%;
  max-width: 45%;
}

.defaultbuttons {
  background-color: black;
  font-family: 'orbitron';
  background: none;
  color: white;
  padding: 0;
}

.button-container {
  background-color: transparent;
  padding: none;
  font-family: 'orbitron';
  background: none;
  color: white;
  display: flex;
  justify-content: space-evenly;
  margin-top: 2rem;

}

.custom-button1 {
  font-size: 18px;
  color: white;
  background-color: transparent;
  border: none;
  text-shadow: 0 0 0px #ffffff,
    0 0 20px #ffffffb8,
    0 0 60px #3d3d3d5a;
}

.custom-button1:hover {
  font-size: 20px;
  color: rgb(0, 255, 123);
  text-shadow: 0 0 5px rgb(0, 255, 123),
    0 0 20px #00ff7bb8,
    0 0 60px #3d3d3d5a;
}

.custom-button2 {
  font-size: 18px;
  background-color: transparent;
  color: white;
  border: none;
  text-shadow: 0 0 0px #ffffff,
    0 0 20px #ffffffb8,
    0 0 60px #3d3d3d5a;
}

.custom-button2:hover {
  font-size: 20px;
  color: rgb(255, 0, 0);
  text-shadow: 0 0 5px rgb(255, 0, 0),
    0 0 20px #d10000c6,
    0 0 60px #4401015a;
}



@media only screen and (max-width: 480px) {

  h1 span:first-child {
    font-size: 25px;
    /* Reduced from 35px */
  }

  h1 span:last-child {
    font-size: 50px;
    /* Reduced from 60px */
    margin-right: 25px;
    top: -50px;
    left: 0px;
  }


  .candlesticks {
    height: 60px;
    width: 50px;
    margin-left: 20px;
    margin-right: none;
    background: none;
  }



  .moon {
    /* Changes position of the moon for mobile devices */
    top: 50%;
    left: 50%;
    width: 200px;
    height: 200px;
  }
}


@media only screen and (max-width: 370px) {

  h1 span:first-child {
    font-size: 15px;
    /* Reduced from 35px */
  }

  h1 span:last-child {
    font-size: 35px;
    /* Reduced from 60px */
    margin-right: 25px;
    top: -50px;
    left: 0px;
  }

  .candlesticks {
    height: 60px;
    width: 50px;
    margin-left: 20px;
    margin-right: none;
    background: none;
  }

  .moon {
    /* Changes position of the moon for mobile devices */
    top: 50%;
    left: 50%;
    width: 200px;
    height: 200px;
  }
}

@media only screen and (max-width: 285px) and (max-height:360px) {

  h1 span:first-child {
    font-size: 15px;
    /* Reduced from 35px */
  }

  h1 span:last-child {
    font-size: 30px;
    /* Reduced from 60px */
    margin-right: 25px;
    top: -50px;
    left: 0px;
  }


  .candlesticks {
    height: 40px;
    width: 50px;
    margin-left: 20px;
    margin-right: none;
    background: none;
  }



  .moon {
    /* Changes position of the moon for mobile devices */
    top: 50%;
    left: 50%;
    width: 100px;
    height: 100px;
  }
}